import { openDB } from 'idb';

const DB_NAME = 'MindMattersDB';
// Increase the version number to trigger a proper upgrade
const DB_VERSION = 2;
let dbPromise = null;
let dbInitialized = false;

// Initialize database with proper initialization check
const getDB = async () => {
  if (!dbPromise) {
    try {
      console.log('Initializing database...');
      dbPromise = openDB(DB_NAME, DB_VERSION, {
        upgrade(db, oldVersion, newVersion, transaction) {
          console.log(`Upgrading database from v${oldVersion} to v${newVersion}`);
          
          // Create all necessary object stores if they don't exist
          if (!db.objectStoreNames.contains('moodEntries')) {
            console.log('Creating moodEntries store');
            const store = db.createObjectStore('moodEntries', { 
              keyPath: 'id', 
              autoIncrement: true 
            });
            store.createIndex('date', 'date', { unique: false });
          }
          
          if (!db.objectStoreNames.contains('settings')) {
            console.log('Creating settings store');
            db.createObjectStore('settings', { keyPath: 'id' });
          }
          
          if (!db.objectStoreNames.contains('themes')) {
            console.log('Creating themes store');
            db.createObjectStore('themes', { keyPath: 'id' });
          }
        }
      });
      
      // Wait for the database to open successfully
      const db = await dbPromise;
      dbInitialized = true;
      console.log('Database initialized successfully with stores:', Array.from(db.objectStoreNames));
      return db;
    } catch (err) {
      console.error('Database initialization failed:', err);
      dbPromise = null;
      return null;
    }
  }
  
  // Wait for the promise to resolve
  try {
    return await dbPromise;
  } catch (err) {
    console.error('Error accessing database:', err);
    dbPromise = null;
    return null;
  }
};

// Enhanced getMoodEntries with better error handling
export const getMoodEntries = async () => {
  try {
    const db = await getDB();
    if (!db) {
      console.warn('Database not available, returning empty entries list');
      return [];
    }
    
    // Verify the object store exists before attempting transaction
    if (!db.objectStoreNames.contains('moodEntries')) {
      console.error('moodEntries store not found in database');
      return [];
    }
    
    return await db.getAll('moodEntries');
  } catch (error) {
    console.error('Error getting mood entries:', error);
    return []; // Return empty array as fallback
  }
};

// Apply the same pattern to other database methods
export const saveJournalEntry = async (entry) => {
  try {
    const db = await getDB();
    if (!db) {
      console.error('Cannot save entry: Database not available');
      return false;
    }
    
    // Verify the object store exists
    if (!db.objectStoreNames.contains('moodEntries')) {
      console.error('Cannot save entry: moodEntries store not found');
      return false;
    }
    
    await db.add('moodEntries', entry);
    console.log('Journal entry saved successfully');
    return true;
  } catch (error) {
    console.error('Error saving journal entry:', error);
    return false;
  }
};

// Add a mood entry
export const addMoodEntry = async (entry) => {
  try {
    const db = await getDB();
    if (!db) {
      console.error('Cannot add entry: Database not available');
      return false;
    }

    // Verify the object store exists
    if (!db.objectStoreNames.contains('moodEntries')) {
      console.error('Cannot add entry: moodEntries store not found');
      return false;
    }

    await db.add('moodEntries', entry);
    console.log('Mood entry added successfully');
    return true;
  } catch (error) {
    console.error('Error adding mood entry:', error);
    return false;
  }
};

// Get mood data function explicitly exported
export const getMoodData = async () => {
  try {
    return await getMoodEntries();
  } catch (error) {
    console.error("Error fetching mood data:", error);
    return []; // Return empty array on error
  }
};

// Clear all mood entries
export const clearMoodEntries = async () => {
  try {
    const db = await getDB();
    if (!db) {
      console.error('Cannot clear entries: Database not available');
      return false;
    }

    // Verify the object store exists
    if (!db.objectStoreNames.contains('moodEntries')) {
      console.error('Cannot clear entries: moodEntries store not found');
      return false;
    }

    await db.clear('moodEntries');
    console.log('All mood entries cleared successfully');
    return true;
  } catch (error) {
    console.error('Error clearing mood entries:', error);
    return false;
  }
};

// Get mood entries by date
export const getMoodEntriesByDate = async (date) => {
  try {
    const entries = await getMoodEntries();
    return entries.filter(entry => entry.date === date);
  } catch (error) {
    console.error('Error getting mood entries by date:', error);
    return [];
  }
};

// Add to storageService.js
export const resetDatabase = async () => {
  try {
    await deleteDB(DB_NAME);
    dbPromise = null;
    dbInitialized = false;
    console.log('Database reset successfully');
    return true;
  } catch (err) {
    console.error('Error resetting database:', err);
    return false;
  }
};