import React, { useState, useEffect, useRef } from 'react';
import './BreathingExercises.css';
import { useTheme } from './ThemeManager';

const BreathingExercises = () => {
    const [activeExercise, setActiveExercise] = useState(null);
    const [isPlaying, setIsPlaying] = useState(false);
    const [counter, setCounter] = useState(0);
    const [phase, setPhase] = useState('');
    const [cycles, setCycles] = useState(0);
    const [selectedCategory, setSelectedCategory] = useState('all');
    const [progress, setProgress] = useState(0);
    const [totalDuration, setTotalDuration] = useState(0);
    const [elapsedTime, setElapsedTime] = useState(0);
    const timerRef = useRef(null);
    const audioRef = useRef(null);
    const phaseTimerRef = useRef(null);
    const countdownIntervalRef = useRef(null);
    const { currentTheme } = useTheme();

    // Breathing exercises data
    const exercises = [
        {
            id: 1,
            name: "4-7-8 Breathing",
            description: "A relaxing breath pattern that helps reduce anxiety and may help you fall asleep faster.",
            category: "relaxation",
            type: "box",
            inhaleTime: 4,
            holdInTime: 7,
            exhaleTime: 8,
            holdOutTime: 0,
            cycles: 5,
            benefits: ["Reduces anxiety", "Helps with sleep", "Lowers heart rate"]
        },
        {
            id: 2,
            name: "Box Breathing",
            description: "A simple technique to help you regain calm and focus. Used by Navy SEALs for stress management.",
            category: "focus",
            type: "box",
            inhaleTime: 4,
            holdInTime: 4,
            exhaleTime: 4,
            holdOutTime: 4,
            cycles: 4,
            benefits: ["Improves concentration", "Manages stress", "Increases mental clarity"]
        },
        {
            id: 3,
            name: "Deep Calm Breathing",
            description: "A gentle technique to promote relaxation and reduce physical tension.",
            category: "relaxation",
            type: "wave",
            inhaleTime: 5,
            holdInTime: 2,
            exhaleTime: 6,
            holdOutTime: 0,
            cycles: 6,
            benefits: ["Reduces physical tension", "Promotes relaxation", "Calms the nervous system"]
        }
    ];

    const startExercise = (exercise) => {
        setActiveExercise(exercise);
        setIsPlaying(true);
        setCounter(exercise.inhaleTime);
        setPhase('inhale');
        setCycles(0);
        setProgress(0);
        setTotalDuration(calculateTotalDuration(exercise));
        setElapsedTime(0);
        
        // Start the phase timer
        startPhaseTimer(exercise);
        
        // Start progress tracking
        const intervalId = setInterval(() => {
            if (!isPlaying) return;
            
            setElapsedTime(prev => {
                const newElapsed = prev + 0.1;
                if (totalDuration > 0) {
                    const progressPercent = (newElapsed / totalDuration) * 100;
                    setProgress(Math.min(progressPercent, 100));
                }
                return newElapsed;
            });
        }, 100);
        
        timerRef.current = intervalId;
    };

    const stopExercise = () => {
        setIsPlaying(false);
        setActiveExercise(null);
        setPhase('');
        setCounter(0);
        setCycles(0);
        setProgress(0);
        setElapsedTime(0);
        
        // Clear all timers
        if (timerRef.current) clearInterval(timerRef.current);
        if (phaseTimerRef.current) clearTimeout(phaseTimerRef.current);
        if (countdownIntervalRef.current) clearInterval(countdownIntervalRef.current);
    };

    const startPhaseTimer = (exercise) => {
        // Clear any existing timer
        if (phaseTimerRef.current) clearTimeout(phaseTimerRef.current);
        
        const currentPhase = phase;
        let duration = 0;
        let nextPhase = '';
        
        // Determine current phase duration and next phase
        switch (currentPhase) {
            case 'inhale':
                duration = exercise.inhaleTime * 1000;
                nextPhase = exercise.holdInTime > 0 ? 'hold-in' : 'exhale';
                break;
            case 'hold-in':
                duration = exercise.holdInTime * 1000;
                nextPhase = 'exhale';
                break;
            case 'exhale':
                duration = exercise.exhaleTime * 1000;
                nextPhase = exercise.holdOutTime > 0 ? 'hold-out' : 'inhale';
                
                // If next phase is inhale and no hold-out, we've completed a cycle
                if (nextPhase === 'inhale') {
                    const newCycles = cycles + 1;
                    setCycles(newCycles);
                    
                    // Check if we've reached the maximum cycles
                    if (exercise.cycles && newCycles >= exercise.cycles) {
                        phaseTimerRef.current = setTimeout(() => {
                            stopExercise();
                        }, duration);
                        return;
                    }
                }
                break;
            case 'hold-out':
                duration = exercise.holdOutTime * 1000;
                nextPhase = 'inhale';
                
                // Always increment cycle count when returning to inhale from hold-out
                const newCycles = cycles + 1;
                setCycles(newCycles);
                
                // Check if we've reached the maximum cycles
                if (exercise.cycles && newCycles >= exercise.cycles) {
                    phaseTimerRef.current = setTimeout(() => {
                        stopExercise();
                    }, duration);
                    return;
                }
                break;
            default:
                // If phase is empty (initial state), set it to inhale
                duration = 0;
                nextPhase = 'inhale';
                break;
        }
        
        // Start countdown for current phase
        startCountdown(
            currentPhase === 'inhale' ? exercise.inhaleTime :
            currentPhase === 'hold-in' ? exercise.holdInTime :
            currentPhase === 'exhale' ? exercise.exhaleTime :
            exercise.holdOutTime
        );
        
        // Set timer for next phase
        phaseTimerRef.current = setTimeout(() => {
            // Set the next phase
            setPhase(nextPhase);
            
            // Set counter for next phase
            if (nextPhase === 'inhale') setCounter(exercise.inhaleTime);
            else if (nextPhase === 'hold-in') setCounter(exercise.holdInTime);
            else if (nextPhase === 'exhale') setCounter(exercise.exhaleTime);
            else if (nextPhase === 'hold-out') setCounter(exercise.holdOutTime);
            
            // Continue the cycle
            startPhaseTimer(exercise);
        }, duration);
    };

    const startCountdown = (seconds) => {
        // Clear any existing interval
        if (countdownIntervalRef.current) clearInterval(countdownIntervalRef.current);
        
        // Set initial counter value
        setCounter(seconds);
        
        // Start countdown interval
        countdownIntervalRef.current = setInterval(() => {
            setCounter(prev => {
                if (prev <= 1) {
                    clearInterval(countdownIntervalRef.current);
                    return 0;
                }
                return prev - 1;
            });
        }, 1000);
    };

    const calculateTotalDuration = (exercise) => {
        const cycleDuration = (
            exercise.inhaleTime +
            exercise.holdInTime +
            exercise.exhaleTime +
            exercise.holdOutTime
        );
        return cycleDuration * exercise.cycles;
    };
    
    // Filter exercises by category
    const filteredExercises = selectedCategory === 'all' 
        ? exercises 
        : exercises.filter(ex => ex.category === selectedCategory);
    
    // Clean up timers on unmount
    useEffect(() => {
        return () => {
            clearTimeout(timerRef.current);
            clearTimeout(phaseTimerRef.current);
            clearInterval(countdownIntervalRef.current);
        };
    }, []);
    
    // Update when phase changes
    useEffect(() => {
        if (isPlaying && activeExercise && phase) {
            // Any additional effects when phase changes
        }
    }, [phase, isPlaying, activeExercise]);

    return (
        <div className="breathing-exercises">
            <h2><span className="breathing-title-icon">🧘</span> Breathing Exercises</h2>
            
            {!activeExercise ? (
                <div>
                    <p className="breathing-intro">
                        Select a breathing technique to help reduce stress, increase focus, or promote relaxation.
                    </p>
                    
                    <div className="category-filter">
                        <button 
                            className={`category-button ${selectedCategory === 'all' ? 'active' : ''}`}
                            onClick={() => setSelectedCategory('all')}
                        >
                            All
                        </button>
                        <button 
                            className={`category-button ${selectedCategory === 'relaxation' ? 'active' : ''}`}
                            onClick={() => setSelectedCategory('relaxation')}
                        >
                            Relaxation
                        </button>
                        <button 
                            className={`category-button ${selectedCategory === 'focus' ? 'active' : ''}`}
                            onClick={() => setSelectedCategory('focus')}
                        >
                            Focus
                        </button>
                    </div>
                    
                    <div className="exercises-list">
                        {filteredExercises.map(exercise => (
                            <div key={exercise.id} className="exercise-card">
                                <div className="exercise-header">
                                    <h3>{exercise.name}</h3>
                                </div>
                                <p>{exercise.description}</p>
                                <div className="exercise-meta">
                                    <span className="duration-tag">
                                        {calculateTotalDuration(exercise) / 60 < 1 
                                            ? `${calculateTotalDuration(exercise)} seconds` 
                                            : `${Math.round(calculateTotalDuration(exercise) / 60)} minutes`}
                                    </span>
                                    <span className="category-tag">{exercise.category}</span>
                                </div>
                                <button 
                                    className="start-button"
                                    onClick={() => startExercise(exercise)}
                                >
                                    Start
                                </button>
                            </div>
                        ))}
                    </div>
                </div>
            ) : (
                <div className="active-exercise">
                    <h3>{activeExercise.name}</h3>
                    
                    <div className="breathing-animation-container">
                        <div className={`breathing-circle ${activeExercise.type} ${phase} ${activeExercise.name === "4-7-8 Breathing" ? "four-seven-eight" : ""}`}>
                            <div className="counter">{counter}</div>
                            <div className="breath-status">{phase.replace('-', ' ')}</div>
                        </div>
                    </div>
                    
                    <div className="progress-container">
                        <div className="progress-bar">
                            <div 
                                className="progress-fill" 
                                style={{width: `${progress}%`}}
                            ></div>
                        </div>
                        <div className="progress-info">
                            Cycles: {cycles}/{activeExercise.cycles || 10}
                        </div>
                    </div>
                    
                    <button 
                        className="stop-button" 
                        onClick={stopExercise}
                    >
                        Stop Exercise
                    </button>
                </div>
            )}
        </div>
    );
};

export default BreathingExercises;